.footer {
	
	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__copyright {
		font-size: 20px;
		color: #B1B1B1;
		margin: 0;
	}
}

.social {

	display: flex;

	&__item {

		padding: 25px 0;
		margin-left: 25px;

		&:first-child {
			margin-left: 0;
		}

		svg {
			width: 25px;
			height: 25px;
			fill: #B1B1B1;
			transition: 0.3s;

			&:hover {
				fill: #42B3E5;
			}
		}
	}
}