.intro {

	&__inner {
		background: url(../img/bg.jpg) center no-repeat;
		background-color: #B2C7DF;
		background-size: cover;

		@media (min-width: 320px) and (max-width: 991px) {
			background: url(../img/bg-mobile.jpg) center no-repeat;
		}
	}

	&__header {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;

		height: 700px;

		@media (min-width: 481px) and (max-width: 991px) {
			height: 525px;
		}

		@media (min-width: 320px) and (max-width: 480px) {
			height: 350px;
		}

		&-title {
			margin-top: 100px;
			margin-left: 100px;

			color: #ffffff;
			font-size: 120px;
			font-weight: 400;
			text-transform: uppercase;
			text-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
			
			@media (min-width: 768px) and (max-width: 991px) {
				font-size: 100px;
				margin-top: 0px;
			}

			@media (min-width: 481px) and (max-width: 767px) {
				font-size: 80px;
				margin-top: 0px;
			}

			@media (min-width: 320px) and (max-width: 480px) {
				font-size: 60px;
				margin-left: 70px;
				margin-top: 0px;
			}
		}

		&-description {
			margin-bottom: 15px;
			margin-left: 100px;

			color: #ffffff;
			font-size: 30px;
			font-weight: 400;
			text-transform: uppercase;
			text-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);

			@media (min-width: 768px) and (max-width: 991px) {
				font-size: 25px;
			}

			@media (min-width: 481px) and (max-width: 767px) {
				font-size: 20px;
				margin-top: 0px;
			}

			@media (min-width: 320px) and (max-width: 480px) {
				font-size: 16px;
				margin-left: 70px;
				margin-top: 0px;
			}
		}

		&-button {
			padding: 15px 40px;
			margin-left: 100px;

			background-color: #3A9DCA;
			border: none;

			color: #ffffff;
			font-size: 18px;
			font-weight: 400;
			text-transform: uppercase;

			cursor: pointer;
			transition: 0.3s;

			@media (min-width: 768px) and (max-width: 991px) {
				font-size: 18px;
			}

			@media (min-width: 481px) and (max-width: 767px) {
				font-size: 16px;
				margin-top: 0px;
			}

			@media (min-width: 320px) and (max-width: 480px) {
				padding: 15px 30px;
				font-size: 12px;
				margin-left: 70px;
				margin-top: 0px;
			}

			&:focus {
				background-color: #093A9C;
				outline: none;
			}
		}
	}
}

// Slick Custom Prev and Next
.prev-arrow {
	position: absolute;
	left: 1%;
	top: 50%;
}

.next-arrow {
	position: absolute;
	right: 1%;
	top: 50%;
}

.prev-arrow, .next-arrow {
	width: 50px;
	height: 50px;
	padding: 10px;

	background-color: #515B61;
	border-radius: 50%;

	z-index: 100;
	cursor: pointer;

	fill: #07161E;
}