.team {

	&__inner {
		padding: 50px 0;
	}

	&__header {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		&-title {
			color: #42b3e5;
			font-size: 90px;
			font-weight: 400;
			line-height: 90px;
			text-transform: uppercase;

			@media (min-width: 320px) and (max-width: 767px) {
				font-size: 70px;
			}
		}

		&-description {
			margin-bottom: 50px;

			color: #737373;
			text-align: center;
			font-size: 24px;
			font-weight: 400;
			text-transform: uppercase;

			@media (min-width: 320px) and (max-width: 767px) {
				font-size: 20px;
			}
		}
	}

	&__doctor {
		display: flex;

		&-item {
			padding: 0 15px;

			@media (min-width: 320px) and (max-width: 767px) {
				padding: 0 5px;
			}
		}

		&-img img {
			display: block;
			width: 100%;
			height: auto;
		}
		
		&--fon-blue {
			background: url(../img/fon-blue.jpg);
			padding: 15px;
		}
		
		&--fon-black {
			background: url(../img/fon-black.jpg);
			padding: 15px;
		}
		
		&-name {
			color: #fff;
			font-size: 30px;
			font-weight: 400;
			text-transform: uppercase;
		}
		
		&-text {
			color: #fff;
			font-family: "Open Sans";
			font-size: 13px;
			font-weight: 400;
			line-height: 20px;
		}
	}
}