.services {
	background: url(../img/fon-blue.jpg);

	&__inner {
		padding: 50px 0;
	}

	&__header {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center; 
		
		&-title {
			text-align: center;
			color: #fff;
			font-size: 90px;
			font-weight: 400;
			line-height: 90px;
			text-transform: uppercase;

			@media (min-width: 320px) and (max-width: 767px) {
				font-size: 70px;
			}
		}

		&-description {
			margin-bottom: 50px;
			
			text-align: center;
			color: #fff;
			text-align: center;
			font-size: 24px;
			font-weight: 400;
			text-transform: uppercase;

			@media (min-width: 320px) and (max-width: 767px) {
				font-size: 20px;
			}
		}
	}

	&__features {
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		justify-content: space-between;

		&-item {
			display: flex;
			flex-direction: column;
			align-items: center; 

			width: 25%;

			@media (min-width: 481px) and (max-width: 767px) {
				width: 50%;
				margin-bottom: 50px;

				&:nth-child(n+2) {
					margin-bottom: 0;
				}
			}

			@media (min-width: 320px) and (max-width: 480px) {
				width: 100%;
				margin-bottom: 50px;

				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		&-svg svg {
			width: 100px;
			height: 100px;

			margin-bottom: 30px;
		}

		&-title {
			color: #fff;
			font-size: 20px;
			font-weight: 400;
			text-transform: uppercase;
		}
	}
}