.contact {

	background: url(../img/maps.jpg) center no-repeat;
	background-size: cover;

	padding: 100px 0;
	&__inner {
		padding: 30px 0 50px;
		background: url(../img/fon-blue.jpg);
	}

	&__header {

		&-title {
			text-align: center;
			color: #fff;
			font-size: 90px;
			font-weight: 400;
			text-transform: uppercase;

			@media (min-width: 320px) and (max-width: 991px) {
				margin-bottom: 50px;
			}

			@media (min-width: 320px) and (max-width: 767px) {
				font-size: 70px;
			}
		}
	}

	&__information {
		width: 50%;
		padding: 0 30px;

		font-size: 18px;
		color: #fff;

		@media (min-width: 320px) and (max-width: 991px) {
			width: 100%;
			margin-bottom: 50px;
		}

		&-title {
			font-size: 30px;
			font-weight: 400;
			text-transform: uppercase;
			padding-bottom: 15px;
			margin-bottom: 15px;
			border-bottom: 3px solid #13849C;

			@media (min-width: 320px) and (max-width: 991px) {
				text-align: center;
			}
		}

		&-time {
			display: flex;
			letter-spacing: 1px;
			margin-bottom: 15px;

			@media (min-width: 320px) and (max-width: 767px) {
				font-size: 16px;
			}

			svg {
				width: 20px;
				height: 20px;
				margin-right: 10px;
			}
		}

		&-address {
			display: flex;
			letter-spacing: 1px;
			margin-bottom: 15px;

			@media (min-width: 320px) and (max-width: 767px) {
				font-size: 16px;
			}

			svg {
				width: 20px;
				height: 20px;
				margin-right: 10px;
			}
		}

		&-phone {
			display: flex;
			letter-spacing: 1px;
			margin-bottom: 15px;

			@media (min-width: 320px) and (max-width: 767px) {
				font-size: 16px;
			}

			svg {
				width: 20px;
				height: 20px;
				margin-right: 10px;
			}
		}

		&-fax {
			display: flex;
			letter-spacing: 1px;
			margin-bottom: 15px;

			@media (min-width: 320px) and (max-width: 767px) {
				font-size: 16px;
			}

			svg {
				width: 20px;
				height: 20px;
				margin-right: 10px;
			}
		}

		&-email {
			display: flex;
			letter-spacing: 1px;

			@media (min-width: 320px) and (max-width: 767px) {
				font-size: 16px;
			}

			svg {
				width: 20px;
				height: 20px;
				margin-right: 10px;
			}
		}
	}

	&__form {
		width: 50%;

		padding: 0 30px;

		font-size: 18px;
		color: #fff;

		@media (min-width: 320px) and (max-width: 991px) {
			width: 100%;
		}

		&-title {
			font-size: 30px;
			font-weight: 400;
			text-transform: uppercase;
			padding-bottom: 15px;
			margin-bottom: 15px;
			border-bottom: 3px solid #13849C;

			@media (min-width: 320px) and (max-width: 991px) {
				text-align: center;
			}
		}

		input {
			border: none;
			width: 100%;
			padding: 7px 15px;
			margin-bottom: 10px;

			font-size: 16px;
			font-weight: 400;
		}

		textarea {
			width: 100%;
			margin-bottom: 10px;
			padding: 7px 15px 70px;

			font-size: 16px;
			font-weight: 400;
			border: none;
		}

		button {
			background: url(../img/fon-black.jpg);
			border: none;

			padding: 10px 50px;

			color: #fff;
			font-size: 20px;
			font-weight: 400;
			text-transform: uppercase;
			cursor: pointer;
		}
	}
}

.contacts {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}