.about {

	&__inner {
		padding: 50px 0;
	}

	&__header {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center; 

		&-title {
			text-align: center;
			color: #42b3e5;
			font-size: 90px;
			font-weight: 400;
			line-height: 90px;
			text-transform: uppercase;

			@media (min-width: 320px) and (max-width: 767px) {
				font-size: 70px;
			}
		}

		&-description {
			margin-bottom: 50px;
	
			text-align: center;
			color: #737373;
			text-align: center;
			font-size: 24px;
			font-weight: 400;
			text-transform: uppercase;

			@media (min-width: 320px) and (max-width: 767px) {
				font-size: 20px;
			}
		}
	}

	&__specialist {
		display: flex;
		flex-direction: row;
		justify-content: space-between;

		&-item {
			padding: 0 15px;

			@media (min-width: 320px) and (max-width: 767px) {
				padding: 0 5px;
			}
		}

		&-img img {
			display: block;
			width: 100%;
			height: auto;
		}

		&--fon-blue {
			background: url(../img/fon-blue.jpg);

			padding: 20px 0;
		}

		&--fon-black {
			background: url(../img/fon-black.jpg);

			padding: 20px 0;
		}

		&-title {
			text-align: center;
			color: #fff;
			font-size: 36px;
			font-weight: 400;
			text-transform: uppercase;
		}

		&-description {
			padding-top: 10px;

			text-align: center;
			padding: 0 50px;

			color: #fff;
			font-size: 16px;
			font-weight: 300;
		}
	}
}